<template>
  <div class="progress-date">
    <div class="titles">
      <span>{{ data.startText }}</span>
      <span>{{ data.endText }}</span>
    </div>
    <div class="progress-bar">
      <div class="progress-bar-color" :style="{ width: value + '%' }"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProgressDate",
  props: {
    data: Object,
  },
  data() {
    return {
      value: 0,
    };
  },
  mounted() {
    const time = Math.floor(Math.random() * 500) + 500;
    setTimeout(() => {
      this.value = this.$props.data.value;
    }, time);
  },
};
</script>

