<template>
  <div class="icon-box">
    <div class="icon" v-lazy:background-image="data.icon"></div>
    <transition name="slide-fade" mode="out-in">
      <div class="value" :key="value">
        {{ value.toFixed(0) }}
      </div>
    </transition>
    <div class="title">{{ data.title }}</div>
  </div>
</template>

<script>
import { gsap } from "gsap";
import { CSSPlugin } from "gsap/CSSPlugin";
gsap.registerPlugin(CSSPlugin);
export default {
  name: "IconBox",
  props: {
    data: Object,
  },
  data() {
    return {
      value: 100,
    };
  },
  mounted() {
    gsap.to(this.$data, { duration: 3, value: this.$props.data.value });
  },
};
</script>

