<template>
  <div class="register-form">
    <h3 class="form-title" v-if="!ok">Registrieren</h3>
    <form class="form" v-if="!ok" autocomplete="off" @submit.prevent="submit">
      <fieldset class="float-label-field">
        <label for="firstname">Vorname</label>
        <input
          @focus="handelFocus"
          @blur="handelBlur"
          :class="{ float: firstname != '' }"
          id="firstname"
          v-model="firstname"
          type="text"
          autocomplete="off"
          required="true"
        />
      </fieldset>
      <fieldset class="float-label-field">
        <label for="lastname">Nachname</label>
        <input
          @focus="handelFocus"
          @blur="handelBlur"
          :class="{ float: lastname }"
          id="lastname"
          v-model="lastname"
          type="text"
          autocomplete="off"
          required="true"
        />
      </fieldset>
      <fieldset class="float-label-field">
        <label for="nickname">Benutzername</label>
        <input
          @focus="handelFocus"
          @blur="handelBlur"
          :class="{ float: nickname }"
          id="nickname"
          v-model="nickname"
          type="text"
          autocomplete="off"
          required="true"
        />
      </fieldset>
      <fieldset class="float-label-field">
        <label for="email">E-Mail-Adresse</label>
        <input
          @focus="handelFocus"
          @blur="handelBlur"
          :class="{ float: email }"
          id="email"
          v-model="email"
          type="email"
          autocomplete="off"
          required="true"
        />
      </fieldset>
      <fieldset class="float-label-field">
        <label for="password">Passwort</label>
        <input
          @focus="handelFocus"
          @blur="handelBlur"
          :class="{ float: password }"
          id="password"
          v-model="password"
          type="password"
          autocomplete="off"
          required="true"
        />
      </fieldset>
      <fieldset class="float-label-field">
        <label for="confirmPassword">Passwort bestätigen</label>
        <input
          @focus="handelFocus"
          @blur="handelBlur"
          :class="{ float: confirmPassword }"
          id="confirmPassword"
          v-model="confirmPassword"
          type="password"
          autocomplete="off"
          required="true"
        />
      </fieldset>
      <fieldset class="float-label-field for-checkbox">
        <input
          :class="{ float: terms }"
          id="terms"
          v-model="terms"
          type="checkbox"
          required="true"
        />
        <label for="terms"
          >Ich akzeptiere
          <router-link to="/register/terms"
            >die Nutzungssbedingungen</router-link
          >
          von Telekom Ideenschmiede.</label
        >
      </fieldset>
      <fieldset class="float-label-field for-checkbox">
        <input
          :class="{ float: privacy }"
          id="privacy"
          v-model="privacy"
          type="checkbox"
          required="true"
        />
        <label for="privacy"
          >Ich habe
          <router-link to="/register/privacy"
            >die Datenschutzhinweise</router-link
          >
          zur Kenntnis genommen.</label
        >
      </fieldset>
      <button class="submit">Jetzt registrieren</button>
    </form>
    <div class="ok" v-if="ok">
      <h3>Vielen Dank für Ihre Registrierung</h3>
      <p>
        Sie erhalten nun eine E-Mail mit einem Aktivierungslink. Bitte
        bestätigen Sie Ihre Registrierung über diesen Link und vervollständigen
        Sie Ihr Profil.
      </p>
      <router-link class="ok-home" to="/">ok</router-link>
    </div>
    <div class="errors" v-if="errors.length > 0">
      <p v-for="(er, idx) in errors" :key="'error' + idx">{{ er }}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
let config = {
  headers: {
    "Content-Type": "application/json",
    "Access-Control-Allow-Methods": "GET, POST, OPTIONS",
    "Access-Control-Allow-Headers": "Content-Type",
  },
};
export default {
  name: "RegisterForm",
  props: {
    data: Object,
    url: String,
  },
  data() {
    return {
      firstname: "",
      lastname: "",
      nickname: "",
      email: "",
      password: "",
      confirmPassword: "",
      privacy: false,
      terms: false,
      ok: false,
      errors: [],
      timer: null,
    };
  },
  methods: {
    handelFocus(e) {
      e.target.closest(".float-label-field").classList.add("float");
    },
    handelBlur(e) {
      if (e.target.value == "")
        e.target.closest(".float-label-field").classList.remove("float");
    },

    validatePassword() {
      var pw = this.password;
      return (
        pw == this.confirmPassword &&
        /[A-Z]/.test(pw) &&
        /[a-z]/.test(pw) &&
        /[0-9]/.test(pw) &&
        /[^A-Za-z0-9]/.test(pw) &&
        pw.length > 4
      );
    },
    submit() {
      if (!this.validatePassword()) {
        this.errors.push("Bitte gültiges Passwort eingeben");
        return;
      }
      const formData = {
        firstname: this.firstname,
        lastname: this.lastname,
        nickname: this.nickname,
        email: this.email,
        password: this.password,
        confirmPassword: this.confirmPassword,
        privacy: this.privacy,
        terms: this.terms,
      };
      var url = this.url;
      if (!url)
        url = document.location.href.substring(
          0,
          document.location.href.indexOf("#")
        );

      axios
        .post(url + "api/register", formData, config)
        .then((response) => {
          var errDe = "Unbekannter Fehler bitte versuchen Sie es später erneut";
          if (response && response.data && response.data.body != "") {
            const body = JSON.parse(response.data.body);
            if (body != "" && body.errors && body.errors.title) {
              if (body.errors.title == "Email is already registered")
                errDe = "E-Mail ist bereits registriert";
              if (body.errors.title == "Invalid email given")
                errDe = "Ungültige E-Mail angegeben";

              this.errors = [...this.errors, errDe];
            }
          } else if (response && response.data && response.data.body == "") {
            this.ok = true;
          }
        })
        .catch((err) => console.log(err));
    },
  },
  watch: {
    errors() {
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.errors = [];
      }, 8000);
    },
  },
};
</script>

