<template>
  <div id="app">
    <transition :name="transitionName" mode="out-in">
      <router-view />
    </transition>
    <div class="bad-screen" v-if="windowWidth / windowHeight != 1.6675">
      <h1>
        Größe sollte 1334 * 800 Pixel sein<br />Bitte ändern Sie die
        Browsergröße
      </h1>
    </div>
    <button id="appInfo" @click="getAppInfo"></button>
  </div>
</template>
<script>
export default {
  name: "App",
  data() {
    return {
      transitionName: "fade1",
      windowHeight: window.innerHeight,
      windowWidth: window.innerWidth,
    };
  },
  mounted() {
    window.addEventListener("resize", this.updateSize);
    console.log(this.windowWidth + " * " + this.windowHeight);
  },
  methods: {
    updateSize() {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
      console.log(this.windowWidth + " * " + this.windowHeight);
    },
    getAppInfo() {
      const { name, version } = this.get_browser();
      alert(
        this.windowWidth +
          "px X " +
          this.windowHeight +
          "px → Browser: " +
          name +
          "\n → version: " +
          version
      );
    },
    get_browser() {
      var ua = navigator.userAgent,
        tem,
        M =
          ua.match(
            /(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i
          ) || [];
      if (/trident/i.test(M[1])) {
        tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
        return { name: "IE", version: tem[1] || "" };
      }
      if (M[1] === "Chrome") {
        tem = ua.match(/\bOPR|Edge\/(\d+)/);
        if (tem != null) {
          return { name: "Opera", version: tem[1] };
        }
      }
      M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, "-?"];
      if ((tem = ua.match(/version\/(\d+)/i)) != null) {
        M.splice(1, 1, tem[1]);
      }
      return {
        ua,
        name: M[0],
        version: M[1],
      };
    },
  },
  watch: {
    $route(to) {
      if (to.name == "Home") this.transitionName = "fade1";
      else this.transitionName = "fade2";
    },
  },
};
</script>

