import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Projects from "../views/Projects.vue";
import Register from "../views/Register.vue";
import VideoPlayer from "../components/VideoPlayer.vue";
import TextPanel from "../components/TextPanel.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    children: [
      {
        path: "video",
        name: "video",
        component: VideoPlayer,
      },
    ],
  },
  {
    path: "/register",
    name: "register",
    component: Register,
    children: [
      {
        path: "terms",
        name: "terms",
        component: TextPanel,
        props: {
          default: true,
          data: "terms",
        },
      },
      {
        path: "privacy",
        name: "privacy",
        component: TextPanel,
        props: {
          default: true,
          data: "privacy",
        },
      },
    ],
  },
  {
    path: "/projects",
    name: "projects",
    component: Projects,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
