<template>
  <div class="text-panel">
    <transition name="fade">
      <div class="text-container" v-if="start">
        <router-link class="close-link" to="/register"></router-link>
        <div v-html="theHtml" />
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "TextPanel",
  props: {
    data: String,
  },
  data() {
    return {
      start: false,
    };
  },
  computed: {
    theHtml() {
      return this.$store.state[this.data];
    },
  },
  mounted() {
    setTimeout(() => {
      this.start = true;
    }, 500);
  },
  beforeRouteLeave(to, from, next) {
    this.start = false;
    setTimeout(() => {
      next();
    }, 500);
  },
};
</script>

<style scoped lang="scss">
.text-panel {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.4);
  padding: 62px;
  opacity: 1;
  overflow: hidden;
  overflow-y: scroll;
  .text-container {
    position: relative;
    width: 100%;
    height: 100%;
    height: fit-content;
    background: #ffffff;
    color: #000;
    font-size: 16px;
    padding: 20px;
    h2,
    h3 {
      font-family: "TeleNeo-Bold";
    }
  }
  .close-link {
    display: block;
    width: 32px;
    height: 32px;
    position: absolute;
    z-index: 10;
    top: 3px;
    right: 3px;

    background-image: url("../assets/close-icon.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.fade-leave-active,
.fade-enter-active {
  transition: all 0.5s ease-in-out;
}
</style>
