<template>
  <div class="video-player">
    <transition name="fade">
      <div class="video" v-if="start">
        <router-link class="close-link" to="/"></router-link>
        <LazyVideo
          :src="videoSrc"
          :poster="videoPoster"
          ref="video"
          :attrs="{
            autoplay: true,
            controls: true,
            loop: false,
            muted: false,
            playsinline: true,
          }"
        />
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "VideoPlayer",
  data() {
    return {
      start: false,
    };
  },

  mounted() {
    setTimeout(() => {
      this.start = true;
    }, 500);
    setTimeout(() => {
      this.video.$el.setAttribute("disablepictureinpicture", true);
      this.video.$el.setAttribute("disableremoteplayback", true);
      this.video.$el.setAttribute(
        "controlsList",
        "nofullscreen nodownload noremoteplayback noplaybackrate"
      );
      this.video.$el.addEventListener("ended", this.reset, false);
    }, 600);
  },
  computed: {
    videoSrc() {
      return this.$store.state.videoSrc;
    },
    videoPoster() {
      return this.$store.state.videoPoster;
    },
    video() {
      return this.$refs.video;
    },
  },
  beforeRouteLeave(to, from, next) {
    this.start = false;
    setTimeout(() => {
      next();
    }, 500);
  },
  methods: {
    reset() {
      this.$router.push({ path: "/" });
    },
  },
};
</script>

