<template>
  <div class="text-accordion-container">
    <div
      v-for="(item, idx) in data"
      :key="'accordion-' + idx"
      class="accordion"
      :class="{ ['accordion-' + idx]: true, isOpen: isOpenIndex == idx }"
    >
      <div class="head" @click="open(idx)">
        <div class="-icon"></div>
        <h4 class="-title" :class="['item-' + idx]" v-html="item.title"></h4>
      </div>
      <div class="-text" v-html="item.text"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TextAccordion",
  props: {
    data: Array,
  },
  data() {
    return {
      isOpenIndex: -1,
    };
  },
  methods: {
    open(index) {
      if (this.isOpenIndex == index) this.isOpenIndex = -1;
      else this.isOpenIndex = index;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.text-accordion-container {
  height: 266px;
  overflow: hidden;
  .accordion {
    margin-bottom: 14px;
    .head {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .-icon {
        width: 36px;
        height: 36px;
        background-image: url("../assets/accordion-icon.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        margin-right: 18px;
        transition: all 0.3s ease-in-out;
      }
      .-title {
        font-family: "TeleNeo-Bold";
        font-size: 18px;
      }
    }
    .-text {
      font-size: 18px;
      padding: 0 0 0 54px;
      line-height: 21px;
      max-height: 0;
      overflow: hidden;
      opacity: 0;
      transition: all 0.5s ease-in-out;
      margin-top: -5px;
    }
    &.isOpen {
      .head {
        .-icon {
          transform: rotate(180deg);
        }
      }
      .-text {
        max-height: 200px;
        opacity: 1;
        transition: all 0.6s ease-in-out;
      }
    }
  }
}
</style>
